import React, { useEffect, useState } from 'react';
import { Link, useHistory, useLocation } from 'react-router-dom';
import axios from 'axios';
import Pagination from "react-js-pagination";
import moment from 'moment';
import { ADDRESS } from '../../../Adress';
import CommonTableRow from '../../../components/table/CommonTableRow';
import CommonTableColumn from '../../../components/table/CommonTableColumn';
import CommonTable from '../../../components/table/CommonTable';





const PostList_ad = props => {
  const history=useHistory();
  const [dataList, setDataList] = useState([]);
  const location = useLocation();

  
  const [pageNumber, setPageNumber] = useState(location.state?.pageNumber || 1);
  const [sortOption, setSortOption] = useState(location.state?.sortOption || 'courseId');
  



  const handlePageChange = (pageNumber) => {
    setPageNumber(pageNumber);
  }
  
  const handleSortOptionChange = (e) => {
    const newSortOption = e.target.value;
    setSortOption(newSortOption);
  }

 

  //    
  useEffect(() => {
    const handlePopState = (event) => {
      const { state } = event;
      if (state) {
        setPageNumber(state.pageNumber);
        setSortOption(state.sortOption);
      }
    };
  
    window.addEventListener('popstate', handlePopState);
  
    return () => {
      window.removeEventListener('popstate', handlePopState);
    };
  }, []);
  
  useEffect(() => {
    axios.get(`${ADDRESS}/courses?page=${pageNumber-1}&sort=${sortOption}`)
      .then(response => {
        console.log(response.data);
        setDataList(response.data.content);
      })
      .catch(error => {
        console.log(error);
      });
    
    //     .
    const state = { pageNumber, sortOption };
    history.replace({ state });
  }, [pageNumber, sortOption]);


const handleDelete = async (courseId) => {
  const token = localStorage.getItem('token');
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        
  try {
    const response = await axios.delete(`${ADDRESS}/users/courses/${courseId}`);
    console.log(response);
    
    //   
    axios.get(`${ADDRESS}/courses?page=${pageNumber-1}`)
      .then(response => {
        console.log(response.data);
        setDataList(response.data.content);
      })
      .catch(error => {
        console.log(error);
       
      });
  } catch (error) {
    console.error(error);
    alert(' .')
  }
};

function handleDeleteConfirm(courseId) {
  const result = window.confirm(`${courseId}  ?`);
  if (result === true) {
   handleDelete(courseId);
  }
  else{ return;}
}
  
  
  return (
    
    <div>
      <div className='background-container'style={{height:'700px'}} >
        <div className='overlay-container'>
          <div className='line'>
             
          </div>
         
         
         
            <div className='select_container'>
              <select value={sortOption} onChange={handleSortOptionChange}>
                <option value="courseId"> </option>
                <option value="like"> </option>
                <option value="scrap"> </option>
                <option value="comment">  </option>
              </select>
            </div>


          <div>
            <>
              <CommonTable
                headersName={[
                  '',
                  '',
                  '',
                  ' ',
                  ' ',
                  '',
                  ''
                ]}
              >
                {dataList
                  ? dataList.map((item, index) => {
                      return (
                        <CommonTableRow key={index}>
                          <CommonTableColumn>{item.courseId}</CommonTableColumn>
                          <CommonTableColumn>
                          <span onClick={() =>history.push({
                              pathname: `/postViewAd/${item.courseId}`,
                              state: { pageNumber, sortOption }
                            })}>
                            {item.courseTitle} ({item.commentCount})
                          </span>
                          </CommonTableColumn>
                          <CommonTableColumn>{item.userId}</CommonTableColumn>
                          <CommonTableColumn>
                         
                            {item.likeCount}
                          </CommonTableColumn>
                          <CommonTableColumn>
                         
                            {item.scrapCount}
                          </CommonTableColumn>
                          <CommonTableColumn>
                            {item. date = moment(item.postedDate).format('YYYY-MM-DD')}
                          </CommonTableColumn>
                         <button className='delBtn' style={{width:'50px',marginTop:'10px'}} onClick={() =>  handleDeleteConfirm(item.courseId)}></button>
                        </CommonTableRow>
                      );
                    })
                  : ''}
              </CommonTable>
            </>
           
          </div>
          
        </div>
      </div>
      <div  style={{marginTop:'30px'}}>
            <Pagination 
              activePage={pageNumber}
              itemsCountPerPage={15}
              totalItemsCount={450}
              pageRangeDisplayed={5}
              prevPageText={""}
              nextPageText={""}
              onChange={handlePageChange}
              itemClass="page-item"
              linkClass="page-link"
              innerClass="pagination"
              prevPageLinkClassName="page-link prev"
              nextPageLinkClassName="page-link next"
            
            />
            </div>  
    </div>
  );
};

export default PostList_ad;
