import React, { useEffect } from 'react'
import { useState,useContext } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import ListGroup from 'react-bootstrap/ListGroup';
import { PersonCircle } from 'react-bootstrap-icons';
import './Mypage.css'
import axios from 'axios';
import { ADDRESS } from '../../Adress';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { useHistory } from 'react-router-dom';





function Mypage() {
  
  const [data,setData]=useState({});
  const token = localStorage.getItem('token');
  const id = localStorage.getItem('id');

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const [showModal, setShowModal] = useState(false);
  const [userMood,setuserMood] = useState('');
  const history = useHistory();

  
  const handleMoodChange = (event) => {
    setuserMood(event.target.value);
  };


  const handleShow = () => {
    setShow(true);
  }



  useEffect(() => {
  
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
    axios.get(`${ADDRESS}/users/${id}`)
      .then(response => {
        //    
        console.log(response.data);
        setData(response.data);
        setuserMood(response.data.userMood);
      })
      .catch(error => {
        //  
        console.error(error);
      });
  }, [token, id]);
  

  
  
  //  
  function handleDeleteConfirm(id) {
    const result = window.confirm(` ?`);
    if (result === true) {
     handleDelete(id);

    }
    else{ return;}
  }

  // 
  const handleDelete = async (id) => {
    const token = localStorage.getItem('token');
    axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
          
    try {
      const response = await axios.delete(`${ADDRESS}/users/${id}`);
      console.log(response.data.message);
      if(response.data.message=='  !'){
      alert(response.data.message);
      localStorage.clear();
      window.location.pathname = '/'}
      else{
        alert(response.data.message);
      }
    } catch (error) {
      console.error(error);
    }
  };
    

  return (
    <div>
      <div style={{display:'flex'}}>
      <div style={{width:'250px',margin:'30px 30px'}}>
         <ListGroup defaultActiveKey='/mypage'>
          <ListGroup.Item action href='/mypage'>
            
          </ListGroup.Item>
          <ListGroup.Item action href='/courses'>
              
          </ListGroup.Item>
          <ListGroup.Item action href='/scraps'>
             
          </ListGroup.Item>
        </ListGroup>
      </div>
      <div className='background-container' id='mypage_background'>
       <div className='overlay-container'>

       <div className='line'> 
      </div>
      <div style={{display:'flex'}}>
      <PersonCircle fontSize={80} style={{margin:'60px 50px'}}color='gray'/>
     <div style={{margin:"55px 20px",fontSize:'14px'}}>
      <div  style={{marginBottom: "10px"}}>  : <span style={{fontWeight:'normal'}}>{data.userName}</span></div>
      <div  style={{marginBottom: "10px"}}>  : <span style={{fontWeight:'normal'}}>{data.userId}</span></div>
      <div style={{display:'flex'}}>
      <span style={{ fontWeight: 'bold' }}>: </span>
      <span style={{ fontWeight: 'normal',marginLeft:'10px',marginRight:'10px' }}>{data.userMood}</span>
    
      <button  className='reBtn' onClick={handleShow} ></button></div></div>
              <Modal show={show} onHide={handleClose}>
               <Modal.Header closeButton>
                 <Modal.Title>  </Modal.Title>
                   </Modal.Header>
                    <Modal.Body className='toCenter' style={{textAlign:'center'}}>
                                                                
                      <div>
                        <input type='radio' id='hip' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                        <label htmlFor='hip'  className={userMood === '' ? 'userMood selected' : 'mood'}></label>
                      </div>
                        
                      <div>
                      <input type='radio' id='healing' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                      <label htmlFor='healing' className={userMood === '' ? 'userMood selected' : 'mood'}></label>
                      </div>
                      
                      <div>
                      <input type='radio' id='retro' name='mood' value='' onChange={handleMoodChange}  style={{display:'none'}} />
                      <label htmlFor='retro' className={userMood === '' ? 'userMood selected' : 'mood'}></label>
                      </div>
                      
                      <div>
                      <input type='radio' id='romantic' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                      <label htmlFor='romantic' className={userMood=== '' ? 'userMood selected' : 'mood'}></label>
                      </div>
                      
                      <div>
                      <input type='radio' id='active' name='mood' value='' onChange={handleMoodChange} style={{display:'none'}} />
                      <label htmlFor='active' className={userMood=== '' ? 'userMood selected' : 'mood'}></label>
                      </div>

                  </Modal.Body>

                <Modal.Footer>
                  <button onClick={handleClose}></button>
                </Modal.Footer>
              </Modal>
      </div>
      
      </div>
      <div className='line'></div>
      <div style={{margin:"50px 50px",fontSize:'13px'}}>
      <button className='delBtn' onClick={() =>  handleDeleteConfirm(data.id)}></button>
      </div>
      </div>
      </div>
    </div>    
    
    
  )
}

export default Mypage;
