import React, { useEffect, useState } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css'; // slick   CSS 
import 'slick-carousel/slick/slick-theme.css'; // slick   CSS 
import './EbayCarousel.css'
import axios from 'axios';


const EbayCarousel = ({ places }) => {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      
      infinite: false,
      

      //    
    };

 

//    
const handleImageClick = (placeId) => {
  //    
  window.location.href = `/place/${placeId}`; //  ID URL  
};

    return (
      <div>
      <Slider {...settings}>
        {/*   map()      */}
        {places.map(place => (
          <div className="carousel-slide" key={place.placeId}>
            <img className="carousel-image" src={place.imageUrl} style={{width:'200px',height:'200px',borderRadius:'10px',marginTop:'20px',marginLeft:'30px'}} 
            onClick={() => handleImageClick(place.placeId)}/>
            <div>{place.placeName}</div>
            {/*      */}
          </div>
        ))}
      </Slider>
      </div>
    );
  };
  
  export default EbayCarousel;
